import React, { useState } from 'react';
import { TextField, Button } from '@material-ui/core';
import { Card, CardContent, Typography, Grid } from '@material-ui/core';
import axios from "axios";
import cx from 'classnames';


import styles from './CountryPicker.module.css';


const CountryPicker = () => {

    const [inputAdvice, setInputAdvice] = useState('');

    const handleChange = (e) => {
        setInputAdvice(e.target.value);
    }

    const handleSumbit = (e) => {
        e.preventDefault();
        setInputAdvice(e.target.value);
    }

    const urlSearch = "https://api.adviceslip.com/advice";
    const [advices, setAdvices] = useState("");

    const fetchAdvice_Search = async () => {
    
      try {
        const respuesta = await axios.get(`${urlSearch}/search/${inputAdvice}`);
        console.log(respuesta.data.slips);
        let a = [];
        a = respuesta.data.slips.map((content) => content.advice);
        setAdvices(a);

        return a;
    
      } catch (error) {
        console.log(error);
      }
    };



    const cards = (
        advices ? (
            <Grid container spacing={3} justify="center">
                {advices.map((item, i) => 
                    <Grid item component={Card} className={cx(styles.Card, styles.Grid)}>
                        <CardContent>
                            <Typography color="textSecondary" variant="h4"  gutterBottom>{inputAdvice}</Typography>
                            <Typography variant="body2" key={i}>{item}</Typography>
                        </CardContent>
                    </Grid>
                )}
            </Grid>
        ) : null
    )
    
    return (
        <div className={styles.container}>
            <form className={styles.formInput} onSubmit={handleSumbit}>
                <TextField type="text" name="city-input" label="Word" variant="outlined" onChange={handleChange}/>
                <Button size="large" variant="contained" className={styles.MuiButton} color="primary" onClick={fetchAdvice_Search}>Search</Button>
            </form>
            {cards}
        </div>
    )
}

export default CountryPicker;