import axios from "axios";

const url = "https://api.adviceslip.com/advice";

export const fetchAdvice = async () => {
  try {
    const respuesta1 = await axios.get(url);
    return respuesta1.data.slip.advice;
  } catch (error) {
    console.log(error);
  }
};

const urlSearch = "https://api.adviceslip.com/advice";

export const fetchAdvice_Search = async () => {
  try {
    const respuesta = await axios.get(urlSearch);
    console.log(respuesta);
    return respuesta.data.slip.advice;

  } catch (error) {
    console.log(error);
  }
};


