import React from 'react';
import { Card, CardContent, Typography, Grid } from '@material-ui/core';

import styles from './Card.module.css';

const Cards = (advice1) => {
    
    if(!advice1){
        return 'Loading...!';
    }
    return (
        <div className={styles.container}>
            <Grid container spacing={3} justify="center">
                <Grid item component={Card} className={styles.adviceOfDay}>
                    <CardContent>
                        <Typography color="textSecondary" variant="h4"  gutterBottom>Advice of the Day!</Typography>
                        <Typography variant="body2">{advice1.advice}</Typography>
                    </CardContent>
                </Grid>
            </Grid>
        </div>
    )
}

export default Cards;